import React from 'react';
import { connect } from 'react-redux';
import { stateToProps } from '../../store/actions/navMenu';
import './banner.scss';
import Profile from './profile';
import ContactIcon from './contactIcon';

export interface ContactList {
  name: string,
  icon: string,
  src?: string,
  info?: string,
}

interface IProp {
  isColorWhite: boolean,
  isMobile: boolean
}

const list: ContactList[] = [
  {
    name: 'github',
    icon: 'icon-github',
    src: 'https://github.com/cangshudada'
  },
  {
    name: '邮箱',
    icon: 'icon-email',
    info: '15342240569@163.com'
  },
  {
    name: '手机',
    icon: 'icon-phone',
    info: '15342240569'
  },
  {
    name: 'QQ',
    icon: 'icon-qq',
    info: '853883909'
  },
  {
    name: 'wechat',
    icon: 'icon-wechat',
    info: 'varon853883909'
  },
]

function genBannerDetails(isColorWhite: boolean, isMobile: boolean) {
  if (!isMobile) {
    return <div>
      <Profile isColorWhite={isColorWhite} />
      <ContactIcon list={list} />
    </div>
  }
}

const Banner = (props: IProp): JSX.Element => {

  const { isColorWhite, isMobile } = props;

  return <div id='banner'>
    {
      genBannerDetails(isColorWhite, isMobile)
    }
    <div>
      LESS IS MORE
    </div>
  </div>
}

export default connect(stateToProps)(Banner);

