import { ModifyAction } from '../../actions/navMenu';
import { UPDATE_MOBILE_SCREEN } from '../../actionType'

export interface HomeReducer {
  isMobile: boolean
}

interface HomeAction {
  type: ModifyAction,
  isMobile: boolean
}

function homeReducer(state: HomeReducer = {
  isMobile: false
}, action: HomeAction): HomeReducer {

  switch (action.type) {

    case UPDATE_MOBILE_SCREEN: {
      return {
        ...state,
        isMobile: action.isMobile
      }
    }

    default: {
      return state;
    }
  }

}

export default homeReducer;