import { CHANGE_MENU, UPDATE_MOBILE_SCREEN } from '../../actionType/index';
import { NavMenuReducer } from '../../reducer/navMenu';
import { HomeReducer } from '../../reducer/home';

export type ModifyAction = CHANGE_MENU;

interface State {
  navMenuReducer: NavMenuReducer[],
  homeReducer: HomeReducer
}

interface StateToProps {
  navMenuList: NavMenuReducer[],
  isMobile: boolean
}

interface DispatchToProps {
  changeNavMenu: (index: number) => void,
  updateMobileScreen: (isMobile: boolean) => void
}

export const stateToProps = (state: State): StateToProps => ({
  navMenuList: state.navMenuReducer,
  isMobile: state.homeReducer.isMobile
})

export const dispatchToProps = (dispatch: Function): DispatchToProps => (
  {
    changeNavMenu: (index: number): void => {
      dispatch({
        type: CHANGE_MENU,
        index
      })
    },
    updateMobileScreen: (isMobile: boolean): void => {
      dispatch({
        type: UPDATE_MOBILE_SCREEN,
        isMobile
      })
    },
  }
)