import React, { useState } from 'react';
import { connect } from 'react-redux';
import { stateToProps, dispatchToProps } from '../../store/actions/navMenu';
import { NavMenuReducer } from '../../store/reducer/navMenu';
import './topNav.scss';

interface IProps {
  navMenuList: NavMenuReducer[],
  changeNavMenu: (index: number) => void,
  isShow: boolean,
  isMobile: boolean
}

/**
 * @param {NavMenuReducer[]} list 需要渲染的menu数组
 * @returns
 */
function genMenu(list: NavMenuReducer[], changeNavMenu: (index: number) => void): JSX.Element[] {

  return list.map((item, index) =>
    <div
      className={`menu ${item.isActive ? '_isMenuActive' : ''}`}
      key={index}
      onClick={() => {
        changeNavMenu(index)
        const anchor = document.getElementById(`${item.id}`) as HTMLElement;
        anchor.scrollIntoView({ behavior: "smooth" });
      }}
    >
      {item.name}
    </div>
  )

}


/**
 *
 * @description 根据不同尺寸屏幕生成nav栏
 * @param {boolean} isMobile
 * @param {NavMenuReducer[]} navMenuList
 * @param {(index: number) => void} changeNavMenu
 * @param {boolean} isShow
 * @returns
 */
function genNav(isMobile: boolean, isShow: boolean, isShowMenu: boolean, navMenuList: NavMenuReducer[], changeNavMenu: (index: number) => void, setMenuShow: (isShowMenu: boolean) => void): JSX.Element {
  if (isMobile) {
    return <div id='top_nav' onClick={() => { setMenuShow(!isShowMenu) }} className={isShowMenu ? '' : 'isHide'}>
      <span></span>
      <span></span>
      <div className={`toggleNav ${isShowMenu ? 'openNav' : ''}`}>
        {
          genMenu(navMenuList, changeNavMenu)
        }
      </div>
    </div>
  } else {
    return <div id='top_nav' className={isShow ? '' : 'isHide'}>
      <div className='avatar_logo'></div>
      <div className="nav_menu">
        {
          genMenu(navMenuList, changeNavMenu)
        }
      </div>
    </div>
  }
}

const TopNav = (props: IProps): JSX.Element => {

  const [isShowMenu, setMenuShow] = useState(false);
  const { navMenuList, changeNavMenu, isShow, isMobile } = props;

  return genNav(isMobile, isShow, isShowMenu, navMenuList, changeNavMenu, setMenuShow)
}


export default connect(stateToProps, dispatchToProps)(TopNav);
