import React, { useState, useRef } from 'react';
import { connect } from 'react-redux';
import { stateToProps, dispatchToProps } from '../store/actions/navMenu';
import { scrollAnimate } from '../utils';
import './home.scss';

import TopNav from '../components/topNav';
import Banner from '../components/banner';
import Content from '../components/content';
import BackTop from '../components/backTop';

interface IProp {
  changeNavMenu: (index: number) => void,
  updateMobileScreen: (isMobile: boolean) => void
}

function updateScreen(updateMobileScreen: (isMobile: boolean) => void) {
  const clientWidth =
    window.innerWidth ||
    document.documentElement.clientWidth ||
    document.body.clientWidth;

  if (clientWidth < 651) {
    updateMobileScreen(true)
  } else {
    updateMobileScreen(false)
  }

  // 视口变化重启AOS动画
  window.addEventListener("resize", () => {

    setTimeout(() => {
      if (clientWidth < 651) {
        updateMobileScreen(true)
      } else {
        updateMobileScreen(false)
      }
    }, 500);
  });
}

const App = (props: IProp): JSX.Element => {
  const home = useRef<HTMLInputElement>(null);
  const [isBackTopShow, setBackTopShow] = useState(false);

  const { changeNavMenu, updateMobileScreen } = props;

  updateScreen(updateMobileScreen)

  // 滚动事件
  const _onScrollEvent = () => {
    if (home.current) {


      if (home.current.scrollTop > 333) {
        setBackTopShow(true)
      } else {
        setBackTopShow(false)
        changeNavMenu(0)
      }
    }
  }

  // 回到顶部
  const backTop = () => {
    if (home.current) {
      scrollAnimate(home.current, 0)
    }
  }

  return <div id="home" ref={home} onScrollCapture={() => _onScrollEvent()}>
    {/* 回到顶部 */}
    <BackTop
      isShow={isBackTopShow}
      onClick={backTop}
    />
    {/* 顶部菜单栏 */}
    <TopNav isShow={!isBackTopShow} />
    {/* banner图部分 */}
    <Banner isColorWhite={!isBackTopShow} />
    {/* 主题项目介绍等 */}
    <Content />
  </div>
}


export default connect(stateToProps, dispatchToProps)(App);